import { updateObject } from './helpers/util.js'

export const setLoading = (loading = true) => {
	return {
		type: "SET_LOADING",
		loading: loading
	}
}

const initialAppState = {
	loading: false
}

function AppReducer(state = initialAppState, action) {
	switch (action.type) {
		// case 'FAUX_LOADING':
		// 	state = updateObject(state, { loading: true });
		case 'SET_LOADING':
			state = updateObject(state, { loading: action.loading });
		default:
			return state;
	}
}
export default AppReducer;
